.manual-irrigation-form {
  display: flex;
  flex-direction: column;
}

.minutes-input-wrapper {
  margin-top: 20px;
}

.breathing-button-wrapper {
  margin-top: 45px;
  margin-bottom: 40px;
}

#minutes-input {
  width: 95px;
}

#breathing-button {
  width: 200px;
  height: 200px;
  border-radius: 50% !important;
  border: 7px solid #cecbcb !important;
  font-size: 16px;
  border-radius: 2px;
  text-align: center;
  text-align: center;
  font-weight: bold;
  font-size: 22px;
}

#breathing-button:active {
  transform: scale(0.9);
}

#breathing-button:disabled {
  transform: scale(0.9);
  border-color: #cac9c9 !important;
}

.breathing {
  animation: breathing 2.5s linear infinite;
  -webkit-font-smoothing: antialiased;
}

@keyframes breathing {
  0% {
    -webkit-transform: scale(0.8);
    -ms-transform: scale(0.8);
    transform: scale(0.8);
  }

  50% {
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
  }

  100% {
    -webkit-transform: scale(0.8);
    -ms-transform: scale(0.8);
    transform: scale(0.8);
  }
}

.timer-wrapper {
  margin-top: 5px;
  margin-bottom: 15px;
  font-size: 28px;
  font-family: 'Roboto Mono', monospace;
}

.manual-loader {
  height: 50px !important;
  width: 50px !important;
}

.error-message {
  font-weight: bold;
  color: red;
}
